<template>
  <div class="gotop">
    <img style="width:4rem;height:4rem;" @click="backTop" class="go-top" v-show="btnFlag" src="http://zmnew319.mb.dlshtsy.net.cn/images/back_top_Z.png" alt="">
  </div>
</template>

<script>

export default {
  name: "Gotop",
  data() {
    return {
      // 返回顶部按钮
      btnFlag: false,
    };
  },
  methods: {
    // 点击回到顶部方法，加计时器是为了过渡顺滑
    backTop() {
      const that = this;
      let timer = setInterval(() => {
        let ispeed = Math.floor(-that.scrollTop / 5);
        document.documentElement.scrollTop = document.body.scrollTop =
          that.scrollTop + ispeed;
        if (that.scrollTop === 0) {
          clearInterval(timer);
        }
      }, 16);
    },

    // 为了计算距离顶部的高度，当高度大于60显示回顶部图标，小于60则隐藏
    scrollToTop() {
      const that = this;
      let scrollTop =
        window.pageYOffset ||
        document.documentElement.scrollTop ||
        document.body.scrollTop;
      that.scrollTop = scrollTop;
      if (that.scrollTop > 60) {
        that.btnFlag = true;
      } else {
        that.btnFlag = false;
      }
    },
  },
  mounted() {
    window.addEventListener("scroll", this.scrollToTop);
  },
  destroyed() {
    window.removeEventListener("scroll", this.scrollToTop);
  },
};
</script>

<style lang="scss">
.gotop {
  .go-top {
    position: fixed;
    right: 1rem;
    bottom: 10rem;
    width: 8rem;
    height: 2rem;
    border-radius: 20px;
    border: 0 solid;
    color: white;
    background-color: black;
    z-index: 1000;
  }
}
</style>
