<template>
  <div class="right_li" v-show="btnFlag">
    <!--右边列表   start-->
    <div class="right-lie">
      <a @click="kf">
        <div class="icon">
          <img
            src="https://i8.mifile.cn/b2c-mimall-media/4f036ae4d45002b2a6fb6756cedebf02.png"
            class="t1"
          />
          <img
            src="https://i8.mifile.cn/b2c-mimall-media/5e9f2b1b0da09ac3b3961378284ef2d4.png"
            class="t2"
          />
        </div>
        <span class="text"> 人工服务 </span>
      </a>

      <a href="#">
        <div class="icon">
          <img src="https://i1.mifile.cn/f/i/2018/home/totop.png" class="t1" />
          <img
            src="https://i1.mifile.cn/f/i/2018/home/totop_hover.png"
            class="t2"
          />
        </div>
        <span class="text"> 返回顶部 </span>
      </a>
    </div>
    <el-dialog
      title="微信客服二维码"
      :visible.sync="centerDialogVisible"
      width="80%"
      center
    >
      <div class="center">
          <div><img src="https://tpl-c17b232-pic48.websiteonline.cn/upload/qr_layerF7EAC0BAA70891C58DB1F936A60CA134.png" alt=""></div>
      </div>
      <span slot="footer" class="dialog-footer">
        <!-- <el-button @click="centerDialogVisible = false">确认</el-button> -->
        <el-button type="primary" @click="centerDialogVisible = false"
          >确 定</el-button
        >
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  name: "right_li",
  data() {
    return {
      // 返回顶部按钮
      btnFlag: false,
      centerDialogVisible:false,
    };
  },
  methods: {
    kf() {
      this.centerDialogVisible = true;
    },
    // 为了计算距离顶部的高度，当高度大于60显示回顶部图标，小于60则隐藏
    scrollToTop() {
      const that = this;
      let scrollTop =
        window.pageYOffset ||
        document.documentElement.scrollTop ||
        document.body.scrollTop;
      that.scrollTop = scrollTop;
      if (that.scrollTop > 60) {
        that.btnFlag = true;
      } else {
        that.btnFlag = false;
      }
    },
  },
  mounted() {
    window.addEventListener("scroll", this.scrollToTop);
  },
  destroyed() {
    window.removeEventListener("scroll", this.scrollToTop);
  },
};
</script>

<style lang="scss" scoped>
.right_li {
  .right-lie {
    position: fixed;
    bottom: 300px;
    right: 0;
    z-index: 99;
  }
  .right-lie a {
    position: relative;
    display: block;
    width: 82px;
    height: 90px;
    margin-top: -1px;
    background-color: #fff;
    border: 1px solid #f5f5f5;
    text-align: center;
  }
  .icon {
    position: relative;
    width: 30px;
    height: 30px;
    margin: 0 auto 8px;
    padding-top: 18px;
  }
  .icon img {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 30px;
    height: 30px;
    transition: opacity 0.3s;
  }
  .text span:hover {
    color: #ff6700;
  }
  .t1 {
    opacity: 1;
  }
  .t2 {
    opacity: 0;
  }
  img {
    border: 0;
  }
  .text {
    color: #757575;
    transition: color 0.3s;
    cursor: pointer;
  }
  .el-dialog--center{
      min-width: 250px;
  }
  .center{
      display: flex;
      justify-content: center;
  }
}
</style>