<template>
  <div class="B_tab">
    <div sticky="true">
      <b-navbar
        toggleable="lg"
        type="light"
        style="background-color: white"
        fixed="top"
      >
        <!-- 手机端logo -->
        <div v-show="show">
          <b-navbar-brand style="user-select: none">
            <img
              :src="img"
              class="d-inline-block align-top"
              alt="Kitten"
              style="widht: 40px; height: 38px"
            />
            <span style="margin-left: 10px">景程装饰</span>
          </b-navbar-brand>
        </div>

        <b-navbar-toggle target="nav-collapse"></b-navbar-toggle>

        <b-collapse id="nav-collapse" is-nav>
          <b-navbar-nav style="margin: 0 auto; width: auto">
            <!-- 电脑端logo -->
            <b-navbar-brand v-show="!show">
              <div class="box">
                <div class="left">
                  <img
                    :src="img"
                    class="d-inline-block align-top"
                    alt="Kitten"
                    style="widht: 50px; height: 50px"
                  />
                </div>
                <div class="right">
                  <div
                    style="
                      font-size: 24px;
                      font-weight: 700;
                      height: 50px;
                      text-align: left;
                    "
                  >
                    景程装饰
                  </div>
                  <div style="font-size: 16px; font-weight: 700; height: 25px">
                    Landscape decoration
                  </div>
                </div>
              </div>
            </b-navbar-brand>

            <b-nav-item
              :active-class="this.$route.meta.active == '1' ? 'color' : ''"
              to="/"
              ><span :class="this.$route.meta.active == '1' ? 'li' : ''"
                >首页</span
              ></b-nav-item
            >
            <b-nav-item
              :active-class="this.$route.meta.active == '2' ? 'color' : ''"
              to="/about"
              ><span :class="this.$route.meta.active == '2' ? 'li' : ''"
                >关于我们</span
              ></b-nav-item
            >
            <b-nav-item
              :active-class="this.$route.meta.active == '3' ? 'color' : ''"
              to="/gcal"
              ><span :class="this.$route.name == 'gcal' ? 'li' : ''"
                >工程案例</span
              ></b-nav-item
            >
            <b-nav-item
              :active-class="this.$route.meta.active == '4' ? 'color' : ''"
              to="/sjtd"
              ><span :class="this.$route.meta.active == '4' ? 'li' : ''"
                >设计团队</span
              ></b-nav-item
            >
            <b-nav-item
              :active-class="this.$route.meta.active == '5' ? 'color' : ''"
              to="/xwzx"
              ><span :class="this.$route.meta.active == '5' ? 'li' : ''"
                >新闻资讯</span
              ></b-nav-item
            >
            <b-nav-item
              :active-class="this.$route.meta.active == '6' ? 'color' : ''"
              to="/lxwm"
              ><span :class="this.$route.meta.active == '6' ? 'li' : ''"
                >联系我们</span
              ></b-nav-item
            >
            
          </b-navbar-nav>
        </b-collapse>
      </b-navbar>
    </div>
  </div>
</template>

<script>
export default {
  name: "B_tab",
  data() {
    return {
      img: require("@/assets/logo.jpg"),
      screenWidth: null,
      show: false,
    };
  },
  methods: {
    _isMobile() {
      let flag = navigator.userAgent.match(
        /(phone|pad|pod|iPhone|iPod|ios|iPad|Android|Mobile|BlackBerry|IEMobile|MQQBrowser|JUC|Fennec|wOSBrowser|BrowserNG|WebOS|Symbian|Windows Phone)/i
      );
      return flag;
    },
  },
  watch: {
    screenWidth: {
      handler: function (val) {
        if (val <= 970) {
          // console.log(val + "屏幕宽度小于900px");
          this.show = true;
        } else {
          // console.log(val + "屏幕宽度大于900px");
          this.show = false;
        }
      },
    },
  },
  created() {},
  mounted() {
    this.screenWidth = document.body.clientWidth;

    window.onresize = () => {
      return (() => {
        this.screenWidth = document.body.clientWidth;
      })();
    };

    if (this._isMobile()) {
      this.show = true;
    } else {
      this.show = false;
      
    }
  },
};
</script>

<style lang="scss" scoped>
.B_tab {
  .nav-item {
    // padding: 30px 40px;
  }
  .nav-item:hover {
    background-color: #996a00;
    text-align: center;
    a {
      text-align: center;
      color: white;
    }
  }
  .nav-item {
    display: flex;
    justify-content: center;
  }
  .nav-link {
    text-align: center;
    padding: 35px;
    width: 150px;
    text-align: center;
  }
  .color {
    background-color: #996a00;
    text-align: center;
  }
  .li {
    text-align: center;
    color: white;
  }
  .wz {
    user-select: none;
  }
  .box {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 220px;
    .left {
    }
    .right {
      height: 86px;
      div {
        display: flex;
        justify-content: center;
        align-items: center;
        // height: 45px;
        font-size: 16px;
      }
    }
  }
}
</style>